<template>
  <div class="correction-page">
    <div class="container">
      <el-card shadow="never">
        <template #header>
          <div class="page-header">
            <h1 class="page-title header-font">作文批改</h1>
            <div
              v-if="
                !essayStore.currentEssay ||
                (essayStore.currentEssay.status !== 'done' &&
                  essayStore.currentEssay.status !== 'processing')
              "
              class="header-actions"
            >
              <el-button @click="handleClearContent">清空内容</el-button>
              <el-button v-if="authStore.isMember" @click="showDrafts = true">草稿列表</el-button>
            </div>
          </div>
        </template>

        <div
          v-if="
            !essayStore.currentEssay ||
            (essayStore.currentEssay.status !== 'done' &&
              essayStore.currentEssay.status !== 'processing')
          "
        >
          <EssayEditor ref="editorRef" />
          <div class="submit-section">
            <el-alert
              v-if="!authStore.isAuthenticated"
              class="tip-alert"
              title="请先登录"
              type="warning"
              :closable="false"
              show-icon
            >
              未登录无法批改作文，<el-link
                type="primary"
                underline="hover"
                @click="$router.push('/login')"
              >
                立即登录
              </el-link>
            </el-alert>
            <div v-if="authStore.isAuthenticated" class="submit-actions">
              <template v-if="authStore.isMember">
                <el-button
                  type="primary"
                  size="large"
                  :loading="essayStore.loading"
                  @click="handleSubmit()"
                >
                  提交批改
                </el-button>
              </template>
              <template v-else>
                <el-button
                  type="primary"
                  size="large"
                  :loading="essayStore.loading"
                  @click="handleSubmit('ink_drops')"
                >
                  使用 100 墨滴批改
                </el-button>
                <el-button
                  type="primary"
                  size="large"
                  plain
                  :loading="essayStore.loading"
                  @click="handleSubmit('quota')"
                >
                  使用 1 次次数卡批改
                </el-button>
              </template>
              <el-button v-if="authStore.isAuthenticated" size="large" @click="handleSaveDraft">
                保存草稿
              </el-button>
            </div>
          </div>
        </div>

        <div v-else-if="essayStore.currentEssay && essayStore.currentEssay.status === 'processing'">
          <el-result icon="loading" title="正在批改中...">
            <template #sub-title>
              <p>AI正在分析您的作文，请稍候</p>
            </template>
          </el-result>
        </div>

        <div v-else-if="essayStore.currentCorrection">
          <div class="result-header">
            <el-button @click="handleReset">重新批改</el-button>
            <el-dropdown trigger="click" @command="handleDownload">
              <el-button>
                下载
                <el-icon class="el-icon--right"><ArrowDown /></el-icon>
              </el-button>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item command="doc">保存为 Word</el-dropdown-item>
                  <el-dropdown-item command="txt">保存为 TXT</el-dropdown-item>
                  <el-dropdown-item command="md">保存为 MD</el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
            <template v-if="authStore.isAuthenticated">
              <el-button
                v-if="!essayStore.currentEssay?.is_favorited"
                type="primary"
                :loading="favoriteLoading"
                @click="handleFavorite"
              >
                收藏
              </el-button>
              <el-button v-else :loading="unfavoriteLoading" @click="handleUnfavorite">
                取消收藏
              </el-button>
            </template>
          </div>
          <CorrectionResult :correction="essayStore.currentCorrection" />
        </div>
      </el-card>
    </div>

    <!-- 草稿列表对话框（与项目弹窗统一样式，小屏自适应） -->
    <el-dialog
      v-model="showDrafts"
      title="草稿列表"
      width="600px"
      class="app-dialog drafts-dialog"
      align-center
    >
      <el-table :data="essayStore.drafts" class="drafts-table" style="width: 100%">
        <el-table-column prop="title" label="标题" />
        <el-table-column prop="updated_at" label="更新时间" width="180">
          <template #default="{ row }">
            {{ new Date(row.updated_at).toLocaleString() }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="140" align="right">
          <template #default="{ row }">
            <div class="draft-op-row">
              <el-button type="primary" text @click="loadDraft(row)">加载</el-button>
              <el-button type="danger" text @click="deleteDraft(row.id)">删除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div v-if="essayStore.drafts.length === 0" class="drafts-empty-state">
        <el-empty description="暂无草稿">
          <el-button type="primary" @click="showDrafts = false">开始编辑</el-button>
        </el-empty>
      </div>
    </el-dialog>

    <!-- 非会员点击保存草稿时提示 -->
    <el-dialog
      v-model="showDraftMemberTip"
      title="开通会员"
      width="400px"
      class="app-dialog"
      align-center
    >
      <p class="draft-member-tip-p">
        <el-link type="primary" underline="hover" @click="closeDraftTipAndGo('/membership')">
          开通会员
        </el-link>
        可保存草稿，可前往
        <el-link type="primary" underline="hover" @click="closeDraftTipAndGo('/essay/history')">
          历史记录
        </el-link>
        查看草稿。
      </p>
      <template #footer>
        <el-button @click="showDraftMemberTip = false">关闭</el-button>
        <el-button type="primary" @click="closeDraftTipAndGo('/membership')"> 去开通 </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, nextTick, watch, h } from 'vue'
import { useRoute, useRouter, onBeforeRouteLeave } from 'vue-router'
import { useAuthStore } from '@/stores/auth'
import { useEssayStore } from '@/stores/essay'
import { unfavoriteEssay } from '@/api/essay'
import { getEssayTemp, setEssayTemp, removeEssayTemp } from '@/utils/essayTempCache'
import EssayEditor from '@/components/Essay/EssayEditor.vue'
import CorrectionResult from '@/components/Essay/CorrectionResult.vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { ArrowDown } from '@element-plus/icons-vue'
import { Document, Packer, Paragraph, HeadingLevel } from 'docx'

const route = useRoute()
const router = useRouter()
const authStore = useAuthStore()
const essayStore = useEssayStore()

const canUseInkDrops = computed(() => (authStore.user?.ink_drops ?? 0) >= 100)
const canUseQuota = computed(() => (authStore.user?.membership?.quota_remaining ?? 0) > 0)
const editorRef = ref<InstanceType<typeof EssayEditor>>()
const showDrafts = ref(false)
const showDraftMemberTip = ref(false)

function closeDraftTipAndGo(path: string) {
  showDraftMemberTip.value = false
  router.push(path)
}

async function loadByRoute() {
  const essayId = route.params.id as string
  if (essayId) {
    await essayStore.fetchEssay(Number(essayId))
  } else {
    essayStore.clearCurrent()
  }
}

/** 当前是否为编辑态（显示题目/类型/内容表单） */
const isEditingState = computed(
  () =>
    !essayStore.currentEssay ||
    (essayStore.currentEssay.status !== 'done' && essayStore.currentEssay.status !== 'processing')
)

onBeforeRouteLeave(() => {
  if (!authStore.isAuthenticated) {
    removeEssayTemp()
    return
  }
  if (isEditingState.value && editorRef.value) {
    const { title, prompt, content_text } = editorRef.value.form
    const t = (title ?? '').trim()
    const p = (prompt ?? '').trim()
    const c = (content_text ?? '').trim()
    if (t || p || c) {
      setEssayTemp({ title: t, prompt: p, content_text: c })
    } else {
      removeEssayTemp()
    }
  }
})

onMounted(async () => {
  await loadByRoute()
  if (authStore.isAuthenticated && authStore.isMember) {
    await essayStore.fetchDrafts()
  }
  // draftId 草稿加载在 watch(editorRef) 中执行（等编辑器挂载后并做未保存确认）
})

function hasUnsavedTemp(): boolean {
  const c = getEssayTemp()
  if (!c) return false
  return !!((c.title ?? '').trim() || (c.prompt ?? '').trim() || (c.content_text ?? '').trim())
}

/** 表单是否有内容（编辑态：含缓存或当前输入） */
function hasEditContent(form: {
  title?: string | null
  prompt?: string | null
  content_text?: string | null
}): boolean {
  return !!(
    (form.title ?? '').trim() ||
    (form.prompt ?? '').trim() ||
    (form.content_text ?? '').trim()
  )
}

// 编辑器挂载后再恢复本地缓存（nextTick 时 ref 可能尚未就绪）
watch(
  editorRef,
  async (el) => {
    if (!el || route.path !== '/essay/correction') return
    const draftId = route.query.draftId as string

    // 1. 重新批改回填（与草稿一致：有编辑态则先确认）
    const pending = essayStore.takePendingResetContent()
    if (pending) {
      if (hasUnsavedTemp() || hasEditContent(el.form)) {
        try {
          await ElMessageBox.confirm('加载将覆盖当前未保存的内容，是否继续？', '提示', {
            confirmButtonText: '继续加载',
            cancelButtonText: '取消',
            customClass: 'app-dialog',
          })
        } catch {
          essayStore.setPendingResetContent(pending)
          return
        }
        removeEssayTemp()
      }
      el.form.title = pending.title
      el.form.prompt = pending.prompt
      el.form.content_text = pending.content_text
      return
    }

    // 2. URL 带 draftId 时加载草稿
    if (draftId) {
      const draft = essayStore.drafts.find((d) => d.id === Number(draftId))
      if (draft) {
        if (hasUnsavedTemp() || hasEditContent(el.form)) {
          try {
            await ElMessageBox.confirm('加载草稿将覆盖当前未保存的内容，是否继续？', '提示', {
              confirmButtonText: '继续加载',
              cancelButtonText: '取消',
              customClass: 'app-dialog',
            })
          } catch {
            router.replace({ path: route.path, query: {} })
            return
          }
          removeEssayTemp()
        }
        el.form.title = draft.title ?? ''
        el.form.content_text = draft.content_text ?? ''
      }
      router.replace({ path: route.path, query: {} })
      return
    }

    // 3. 无重新批改/草稿时恢复本地缓存
    const cached = getEssayTemp()
    if (!cached || (!cached.title && !cached.prompt && !cached.content_text)) return
    el.form.title = cached.title
    el.form.prompt = cached.prompt
    el.form.content_text = cached.content_text
    ElMessage.info('已恢复上次未保存的编辑')
  },
  { flush: 'post' }
)

// 同一组件复用：从 /essay/correction 进入 /essay/:id 时需根据 id 拉取批改结果
watch(
  () => route.params.id,
  async () => {
    await loadByRoute()
  }
)

async function handleSubmit(payType?: 'ink_drops' | 'quota') {
  if (!editorRef.value) return

  const { form } = editorRef.value
  if (!form.content_text.trim()) {
    ElMessage.warning('请输入作文内容')
    return
  }

  // 非会员：余额或次数不足时拦截并弹窗提示
  if (!authStore.isMember && payType) {
    if (payType === 'ink_drops' && !canUseInkDrops.value) {
      await ElMessageBox.alert(
        h('p', null, [
          '请 ',
          h(
            'span',
            {
              class: 'ink-shortage-link',
              onClick: () => {
                ElMessageBox.close()
                router.push('/membership')
              },
            },
            '升级会员'
          ),
          ' 获得更多服务',
        ]),
        '墨滴不足',
        { confirmButtonText: '确定', customClass: 'app-dialog' }
      )
      return
    }
    if (payType === 'quota' && !canUseQuota.value) {
      const goMembership = () => {
        ElMessageBox.close()
        router.push('/membership')
      }
      await ElMessageBox.alert(
        h('p', null, [
          '请 ',
          h('span', { class: 'ink-shortage-link', onClick: goMembership }, '兑换次数卡'),
          ' 或 ',
          h('span', { class: 'ink-shortage-link', onClick: goMembership }, '升级会员'),
          ' 以获得更多服务',
        ]),
        '次数不足',
        { confirmButtonText: '确定', customClass: 'app-dialog' }
      )
      return
    }

    // 消耗确认弹窗：提示花费多少、用于什么、当前余额，用户可确认或取消
    if (payType === 'ink_drops') {
      const confirmed = await ElMessageBox.confirm(
        `将消耗 100 墨滴用于本次作文批改。\n当前墨滴：${authStore.user?.ink_drops ?? 0}`,
        '消耗确认',
        {
          confirmButtonText: '确认继续',
          cancelButtonText: '取消',
          type: 'info',
          customClass: 'app-dialog',
        }
      ).catch(() => false)
      if (!confirmed) return
    }
    if (payType === 'quota') {
      const quota = authStore.user?.membership?.quota_remaining ?? 0
      const confirmed = await ElMessageBox.confirm(
        `将消耗 1 次次数卡用于本次作文批改。\n当前剩余：${quota} 次`,
        '消耗确认',
        {
          confirmButtonText: '确认继续',
          cancelButtonText: '取消',
          type: 'info',
          customClass: 'app-dialog',
        }
      ).catch(() => false)
      if (!confirmed) return
    }
  }

  const payload: Parameters<typeof essayStore.submitEssay>[0] = {
    title: form.title,
    prompt: form.prompt,
    content_text: form.content_text,
    source_type: 'text',
  }
  if (payType) payload.pay_type = payType

  try {
    await essayStore.submitEssay(payload)
    removeEssayTemp()
    // 刷新用户信息（墨滴/次数卡已扣减，保证各处显示正确）
    await authStore.fetchUser()

    // 轮询检查批改结果；fetchEssay 在 status===done 时已拉取完整批改（含 diff_html），勿用 essay.correction 覆盖
    const checkInterval = window.setInterval(async () => {
      if (essayStore.currentEssay) {
        await essayStore.fetchEssay(essayStore.currentEssay.id)
        if (
          essayStore.currentEssay.status === 'done' ||
          essayStore.currentEssay.status === 'failed'
        ) {
          window.clearInterval(checkInterval)
        }
      }
    }, 2000)

    // 10秒后停止轮询
    window.setTimeout(() => window.clearInterval(checkInterval), 10000)
  } catch (error: unknown) {
    const msg = (error as Error).message || '提交失败'
    ElMessage.error(msg)
    if (msg.includes('不足') || msg.includes('兑换')) {
      nextTick(() => {
        router.push('/membership')
      })
    }
  }
}

function handleClearContent() {
  editorRef.value?.clear()
  removeEssayTemp()
  ElMessage.success('已清空')
}

async function handleReset() {
  // 在跳转前于当前页检查未保存缓存，新组件挂载时表单为空无法判断
  if (hasUnsavedTemp()) {
    try {
      await ElMessageBox.confirm('加载将覆盖当前未保存的内容，是否继续？', '提示', {
        confirmButtonText: '继续加载',
        cancelButtonText: '取消',
        customClass: 'app-dialog',
      })
    } catch {
      return
    }
    removeEssayTemp()
  }
  if (essayStore.currentEssay) {
    essayStore.setPendingResetContent({
      title: essayStore.currentEssay.title ?? '',
      prompt: essayStore.currentEssay.prompt ?? '',
      content_text: essayStore.currentEssay.content_text ?? '',
    })
  }
  essayStore.clearCurrent()
  router.push('/essay/correction')
}

/** 从 diff_html 提取修改后的正文（去掉删除内容，保留插入和原文） */
function getModifiedContent(diffHtml: string): string {
  const div = document.createElement('div')
  div.innerHTML = diffHtml
  const removeDels = (node: Node): void => {
    if (node.nodeType === Node.ELEMENT_NODE) {
      const el = node as Element
      if (el.tagName === 'DEL') {
        el.remove()
        return
      }
      const children = Array.from(node.childNodes)
      children.forEach(removeDels)
    }
  }
  removeDels(div)
  return (div.innerText || div.textContent || '').trim()
}

function getDownloadFilename(ext: string): string {
  const title = essayStore.currentEssay?.title || '批改结果'
  const safe = title.replace(/[/\\?*:|"]/g, '_').slice(0, 80)
  return `${safe}_${new Date().toISOString().slice(0, 10)}.${ext}`
}

async function handleDownload(format: 'doc' | 'txt' | 'md') {
  if (!essayStore.currentEssay || !essayStore.currentCorrection) return
  const title = essayStore.currentEssay.title || '批改结果'
  const content = getModifiedContent(essayStore.currentCorrection.diff_html)

  if (format === 'txt') {
    const txt = [title, '', content].join('\n')
    const blob = new Blob(['\uFEFF' + txt], { type: 'text/plain;charset=utf-8' })
    downloadBlob(blob, getDownloadFilename('txt'))
    ElMessage.success('已保存为 TXT')
    return
  }

  if (format === 'md') {
    const md = [`# ${title}`, '', content].join('\n')
    const blob = new Blob(['\uFEFF' + md], { type: 'text/markdown;charset=utf-8' })
    downloadBlob(blob, getDownloadFilename('md'))
    ElMessage.success('已保存为 MD')
    return
  }

  // 使用 docx 库生成真实 .docx（Office Open XML），文档默认字体：汉字宋体，英文/数字新罗马
  const defaultFont = {
    eastAsia: '宋体',
    ascii: 'Times New Roman',
    hAnsi: 'Times New Roman',
  }
  const titlePara = new Paragraph({ text: title, heading: HeadingLevel.HEADING_1 })
  const contentParas = content.split(/\n/).map((line) => new Paragraph(line || ' '))
  const doc = new Document({
    styles: {
      default: {
        document: {
          run: { font: defaultFont },
        },
      },
    },
    sections: [{ children: [titlePara, new Paragraph(''), ...contentParas] }],
  })
  const blob = await Packer.toBlob(doc)
  downloadBlob(blob, getDownloadFilename('docx'))
  ElMessage.success('已保存为 Word')
}

function downloadBlob(blob: Blob, filename: string) {
  const a = document.createElement('a')
  a.href = URL.createObjectURL(blob)
  a.download = filename
  a.click()
  URL.revokeObjectURL(a.href)
}

const favoriteLoading = ref(false)
const unfavoriteLoading = ref(false)

async function handleFavorite() {
  if (!essayStore.currentEssay) return
  favoriteLoading.value = true
  try {
    await essayStore.favoriteEssay(essayStore.currentEssay.id)
    essayStore.setCurrentEssayFavorited(true)
    ElMessage.success('收藏成功')
    essayStore.fetchEssay(essayStore.currentEssay.id).catch(() => {})
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '收藏失败')
  } finally {
    favoriteLoading.value = false
  }
}

async function handleUnfavorite() {
  if (!essayStore.currentEssay) return
  unfavoriteLoading.value = true
  try {
    await unfavoriteEssay(essayStore.currentEssay.id)
    essayStore.setCurrentEssayFavorited(false)
    ElMessage.success('已取消收藏')
    essayStore.fetchEssay(essayStore.currentEssay.id).catch(() => {})
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '取消收藏失败')
  } finally {
    unfavoriteLoading.value = false
  }
}

async function handleSaveDraft() {
  if (!authStore.isAuthenticated) {
    ElMessage.warning('请先登录')
    router.push('/login')
    return
  }
  if (!authStore.isMember) {
    showDraftMemberTip.value = true
    return
  }
  if (!editorRef.value) return

  const { form } = editorRef.value
  if (!form.content_text.trim()) {
    ElMessage.warning('请输入作文内容')
    return
  }

  try {
    await essayStore.createDraft({
      title: form.title,
      content_text: form.content_text,
    })
    removeEssayTemp()
    ElMessage.success('草稿已保存')
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '保存失败')
  }
}

async function loadDraft(draft: { title?: string; content_text: string }) {
  if (!editorRef.value) return
  if (hasUnsavedTemp() || hasEditContent(editorRef.value.form)) {
    try {
      await ElMessageBox.confirm('加载草稿将覆盖当前未保存的内容，是否继续？', '提示', {
        confirmButtonText: '继续加载',
        cancelButtonText: '取消',
        customClass: 'app-dialog',
      })
    } catch {
      showDrafts.value = false
      return
    }
    removeEssayTemp()
  }
  editorRef.value.form.title = draft.title || ''
  editorRef.value.form.content_text = draft.content_text
  showDrafts.value = false
}

async function deleteDraft(id: number) {
  try {
    await ElMessageBox.confirm('确定要删除这个草稿吗？', '提示', {
      type: 'warning',
      customClass: 'app-dialog',
    })
    await essayStore.deleteDraft(id)
    ElMessage.success('删除成功')
  } catch (e) {
    if (e !== 'cancel') {
      ElMessage.error((e as Error).message || '删除失败')
    }
  }
}
</script>

<style scoped>
.correction-page {
  min-height: calc(100vh - var(--header-height) - 80px);
  padding: var(--spacing-lg) 0;
  background: transparent;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 var(--spacing-md);
}

.page-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: flex-start;
  gap: var(--spacing-sm);
}

.page-header .page-title {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

/* 同排时靠右（由 page-header space-between 控制），换行后整块在下一行左对齐 */
.header-actions {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
  gap: var(--spacing-sm);
}

.header-actions .el-button {
  margin: 0;
}

.submit-section {
  margin-top: var(--spacing-lg);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
