<template>
  <div class="correction-result">
    <el-card shadow="never" class="stats-card">
      <template #header>
        <h3 class="header-font">语言学统计</h3>
      </template>
      <div class="stats-grid">
        <div class="stat-item">
          <div class="stat-value">{{ correction.stats.word_count }}</div>
          <div class="stat-label">字数</div>
        </div>
        <div class="stat-item">
          <div class="stat-value">{{ correction.stats.paragraph_count }}</div>
          <div class="stat-label">段落数</div>
        </div>
        <div class="stat-item">
          <div class="stat-value">{{ correction.stats.typo_count }}</div>
          <div class="stat-label">错别字</div>
        </div>
        <div class="stat-item" v-if="correction.stats.sentence_count">
          <div class="stat-value">{{ correction.stats.sentence_count }}</div>
          <div class="stat-label">句子数</div>
        </div>
      </div>
    </el-card>

    <el-card shadow="never" class="summary-card">
      <template #header>
        <h3 class="header-font">评语与建议</h3>
      </template>
      <p class="summary-text">{{ correction.summary }}</p>
    </el-card>

    <el-card shadow="never" class="diff-card">
      <template #header>
        <div class="diff-card-header">
          <h3 class="header-font">批改结果</h3>
          <label class="diff-toggle-wrap">
            <span class="diff-toggle-label">显示修改痕迹</span>
            <el-switch v-model="showDiff" style="--el-switch-on-color: var(--color-primary)" />
          </label>
        </div>
      </template>
      <div v-if="showDiff" class="diff-content" v-html="correction.diff_html"></div>
      <div v-else class="diff-content diff-content-plain">{{ modifiedContent }}</div>
    </el-card>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue'
import type { Correction } from '@/types/essay'

const props = defineProps<{
  correction: Correction
}>()

const showDiff = ref(true)

/** 从 diff_html 去掉删除内容，得到仅修改后的正文 */
function getModifiedContent(diffHtml: string): string {
  const div = document.createElement('div')
  div.innerHTML = diffHtml
  const removeDels = (node: Node): void => {
    if (node.nodeType === Node.ELEMENT_NODE) {
      const el = node as Element
      if (el.tagName === 'DEL') {
        el.remove()
        return
      }
      const children = Array.from(node.childNodes)
      children.forEach(removeDels)
    }
  }
  removeDels(div)
  return (div.innerText || div.textContent || '').trim()
}

const modifiedContent = computed(() => getModifiedContent(props.correction.diff_html))
</script>

